// types/sospechosos.ts
/**
 * Modelos y datos mock para el “tablón de buscados” del Kwik-E-Mart.
 *
 * Incluye:
 * - `Sospechoso` — interface principal.
 * - `sospechosos` — lista seed para demos o pruebas.
 * - `frasesSimpson` — array de frases célebres para usar como easter-eggs.
 * - `nivelPeligroConfig` — mapa con metadatos de cada nivel de peligro
 *   (color, descripción corta y prioridad numérica).
 */

/* -------------------------------------------------------------------------- */
/*                                   Tipos                                    */
/* -------------------------------------------------------------------------- */

/**
 * Sospechoso buscado por el Kwik-E-Mart Security Department.
 *
 * @property id             Id único.
 * @property nombre         Nombre real (o más usado).
 * @property descripcion    Resumen de sus “hazañas”.
 * @property imagen         Ruta a imagen o avatar.
 * @property ultimaVezVisto String breve con la última vez avistado.
 * @property nivelPeligro   Gravedad (BAJO → EXTREMO).
 * @property recompensa     $ si procede (opcional).
 * @property alias          AKA, motes conocidos (opcional).
 * @property delitos        Lista textual de fechorías (opcional).
 */
export interface Sospechoso {
    id: number;
    nombre: string;
    descripcion: string;
    imagen: string;
    ultimaVezVisto: string;
    nivelPeligro: "BAJO" | "MEDIO" | "ALTO" | "EXTREMO";
    recompensa?: number;
    alias?: string[];
    delitos?: string[];
}

